import { dispatch } from 'utils'
import { INTERNAL_EVENT_IN_NAME } from 'const'
import { INTERNAL_OUT, INTERNAL_IN } from '../const'
/*
 * Injected script is used to gain access to JS context
 */

// window.onbeforeunload = function() { if(document.body.classList.contains('blockunload')) { debugger; return 'Action in progress'; } };

setTimeout(function () {
  document.addEventListener(INTERNAL_EVENT_IN_NAME, function (e) {
    switch (e.detail.topic) {
      case INTERNAL_IN.REQUEST_SOMETHING: {
        const data = e.detail.data // eslint-disable-line
        // Process data

        // Post response:
        dispatch(INTERNAL_OUT.SOMETHING_RESPONSE, { processed: 'data' })
        break
      }
    }
  })
}, 0)
