import each from 'lodash/each'
import PubSub from 'pubsub-js'
import availableSites from 'available_sites'
import { BACKGROUND_EVENTS, INTERNAL_EVENT_OUT_NAME, BACKGROUND, CONTENT } from 'const'

/*
 * PubSubJs calls callback with (eventName, data). It wraps callbacks and removes redundant eventName
 */
export const skipEventNameArgument = callback => (_eventName, data) => callback(data)
export const bindEvents = eventsHash => {
  each(eventsHash, (callback, event) => PubSub.subscribe(event, skipEventNameArgument(callback)))
}
export const publish = PubSub.publish

export function performInjection (filename) {
  const s = document.createElement('script')
  s.src = chrome.extension.getURL(filename);
  (document.head || document.documentElement).appendChild(s)
  s.onload = function () {
    s.remove()
  }
}

export function getStoreNameByUrl (urlToCheck) {
  if (!urlToCheck) return null

  const stringUrl = String(urlToCheck)

  for (const siteName in availableSites) {
    const site = availableSites[siteName]
    if (stringUrl.indexOf(site.url) !== -1) return site.name

    for (const url of site.alternativeUrls) {
      if (stringUrl.indexOf(url) !== -1) return site.name
    }
  }
}

/*
 * Dispatches event from injected script to the content script
 */
export function dispatch (topic, data = {}) {
  document.dispatchEvent(
    new CustomEvent(INTERNAL_EVENT_OUT_NAME, { detail: { topic: topic, data: data } })
  )
}

export function captureError (store, message, error, data) {
  chrome.runtime.sendMessage({
    topic: BACKGROUND_EVENTS.CAPTURE_ERROR,
    store,
    message,
    error,
    data
  })
}

export function extractDomain (url) {
  let protocol = ''
  const protocolIndex = url.indexOf('//')
  if (protocolIndex > -1) {
    protocol = url.substr(0, protocolIndex + 2)
  }
  const a = document.createElement('a')
  a.href = url
  return protocol + a.hostname
}

export function getContext () {
  /*
  Return background if executed inside the background script, content if in the content script.
   */
  if (location.protocol === 'chrome-extension:' && chrome.extension.getBackgroundPage() === window) {
    return BACKGROUND
  } else {
    return CONTENT
  }
}
