const flatMap = require('lodash/flatMap')
const flatten = require('lodash/flatten')

const stores = ['aliexpress']

const sites = {}
stores.forEach(storeName => {
  sites[storeName] = require(`./stores/${storeName}/config.js`)
})

for (const key in sites) {
  const site = sites[key]
  const { url, alternativeUrls } = site

  const urls = flatten([url, alternativeUrls])
  site.url_matchers = flatMap(urls, url => [
    `http://${url}/*`,
    `http://*.${url}/*`,
    `https://${url}/*`,
    `https://*.${url}/*`
  ])
}

module.exports = sites
